@font-face {
   font-family: Gotham Black;
   src: url(\public\fonts\GothamBlack.ttf); }

@font-face {
   font-family: Gotham Book;
   src: url(\public\fonts\GothamBook.otf); }

@font-face {
   font-family: Gotham;
   src: url(\public\fonts\Gotham.ttf); }

$color-main-text: rgb(255, 255, 255);
$color-sub-text: rgb(205,20,168);
$colorBg-body: rgb(0, 0, 0);
$color-page: rgb(32,31,31);
$color-detail-text: rgba(255, 255, 255, .4);

$borderRadius-page: 8px;
$boxShadow-page: 0 3px 145px rgba(0, 0, 0, 0.92);
$boxShadow-preview: 0 3px 145px rgba(0, 0, 0, 0.38);

body {
   max-width: 100vw;
   min-height: 100vh;
   height: auto;
   display: flex;
   background-color: $colorBg-body;
   margin: 0;
   color: $color-main-text;
   box-sizing: border-box;
   font-family: Gotham; }

h1,
h2, {
   font-family: Gotham;
   margin: 0;
   padding: 0;
   width: auto;
   text-align: center; }

button,
button:hover,
button:focus,
button:active,
button:visited,
a,
a:hover,
a:focus,
a:active,
a:visited,
summary {
   border: none;
   background: none;
   color: inherit;
   outline: none;
   cursor: pointer;
   font-family: Gotham; }

p {
   font-family: Gotham Book;
   margin: 0; }

p::selection,
span::selection,
h1::selection,
h2::selection,
a::selection,
button::selection {
   background: $color-sub-text; }

.wrapper {
   display: flex;
   flex-direction: column;
   height: auto;
   min-height: 80vh;
   width: calc( 100% - 16.7% );
   margin: auto;
   box-sizing: border-box;
   box-shadow: $boxShadow-page;
   border-radius: $borderRadius-page;
   background-color: $color-page;
   padding: 5vh 5vw; }

.wrapper_home {
   padding: 0;
   background: $colorBg-body; }

.about {
    display: flex;
    flex-direction: column;
    align-items: center; }

.about__wrapper {
    display: flex; }

.about__info {
    display: flex;
    flex-direction: column;
    justify-content: space-around; }

.about__title {
    color: $color-sub-text; }

.about__skills {
    display: flex;
    flex-direction: column;
    align-self: flex-end; }

.about__skills span {
    font-weight: 400;
    letter-spacing: 0.3px;
    font-family: Gotham Book;
    margin-bottom: 10px; }

.btn {
    color: $color-main-text;
    text-decoration: none; }

.btn:hover {
    color: $color-sub-text; }

.contact {
    display: flex; }

.contact__info {
    display: flex;
    flex-direction: column;
    justify-content: space-around; }

.contact__title {
    color: $color-sub-text; }

.contact__adres {
    display: flex;
    flex-direction: column;
    align-self: flex-end;
    letter-spacing: 0.3px;
    font-family: Gotham Book; }

.contact__adres span {
    margin-bottom: 10px; }

.contact__sity {
    display: flex;
    justify-content: flex-end;
    font-weight: 900; }

.detail-project {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column; }
.detail-text {
    color: $color-detail-text;
    font-family: Gotham Book;
    letter-spacing: 0.4px;
    text-align: justify;
    flex-direction: column; }

.detail-text__full[open] > summary {
    display: none; }

.detail-text__full > summary::-webkit-details-marker {
    display: none; }

.footer {
   flex: 0 0 auto;
   display: flex;
   justify-content: space-between;
   color: #8f8f8f;
   flex-direction: column-reverse;
   align-items: center; }

.footer__copyright {
   letter-spacing: 1px;
   font-size: calc( 12em / 20em );
   margin-top: 15px; }

.footer__copyright span {
   letter-spacing: 0.5px;
   text-transform: uppercase; }

.footer__social {
   width: 80%;
   display: flex;
   justify-content: space-between; }

.header {
   display: flex;
   flex-direction: column;
   align-items: flex-end; }

.header__logo {
   letter-spacing: 3px;
   font-weight: 900;
   font-size: 40px;
   display: flex;
   align-items: center;
   position: relative; }

.header__logo:before {
   content: '';
   width: 50px;
   height: 3px;
   position: absolute;
   top: 35px;
   left: 65px;
   background: $color-main-text; }

.header__logo span:nth-child(1) {
   font-family: Gotham Black;
   font-size: 55px;
   letter-spacing: 100px;
   color: $color-sub-text; }

.header__navigation {
   display: flex;
   flex-direction: column;
   align-items: flex-end; }

.header__menu-btn {
   position: relative;
   text-transform: uppercase;
   z-index: 10;
   margin-top: 20px; }

.header__menu-btn:before {
   content: '';
   position: absolute;
   top: 50%;
   left: -15%;
   width: 0;
   height: 3px;
   background: $color-sub-text;
   transition: all .75s ease; }

.header__menu-btn:hover:before {
   width: 130%; }

.header__navigation:hover .header__dropdown {
   padding-top: 70px;
   visibility: visible;
   opacity: 1;
   transition-delay: 0s; }

.header__dropdown {
   display: flex;
   flex-direction: column;
   align-items: flex-end;
   justify-content: space-between;
   font-size: calc( 35em / 20em );
   position: absolute;
   padding-top: 30px;
   opacity: 0;
   visibility: hidden;
   transition: opacity .75s, padding-top .75s, visibility 0s linear .75s; }

.horizontal-line {
   position: relative; }

.horizontal-line:before,
.horizontal-line:after {
   content: '';
   position: absolute;
   top: 50%;
   background: $color-main-text;
   height: 3px;
   width: 40px; }

.horizontal-line:before {
   left: 0; }

.horizontal-line:after {
   right: 0; }

.load-more {
   display: flex;
   flex-direction: column;
   align-items: center;
   position: relative; }

.load-more span {
   margin: 15px 0; }

.main {
   flex: 1 0 auto;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center; }

.home-main {
   flex-direction: column;
   align-items: baseline; }

.home-main__sub-title {
   text-align: center;
   margin-left: 40px;
   width: 78%; }

.home-main__title span {
   color: $color-sub-text; }

.main__sub-title span {
   color: $color-sub-text; }

.home-main__sub-title.horizontal-line:after {
   content: none; }


.page-404 {
    margin-bottom: 25px;
    margin-top: 100px; }

.page-404__error {
    letter-spacing: -6.25px;
    color: $color-sub-text;
    font-family: Gotham;
    position: relative; }

.page-404__error:before {
    content: '';
    width: 135%;
    height: 1px;
    background: $color-sub-text;
    position: absolute;
    top: 50%;
    left: -17.5%; }

.page-404__title {
    color: $color-sub-text; }

.preview {
    box-shadow: $boxShadow-preview;
    background-color: $colorBg-body;
    height: 0; }
.project-wrapper {
   width: 100%;
   position: relative;
   display: grid;
   grid-gap: 100px; }

.project {
   width: 100%; }

.project__info {
   display: grid;
   grid-template-rows: min-content auto min-content;
   z-index: 1; }

.project__title {
   text-align: left; }

.project__description {
   letter-spacing: 0.18px; }

.project__btn-detail {
   position: relative;
   display: flex;
   width: fit-content;
   align-items: center;
   min-height: 20px;
   margin-bottom: 10px; }

.project__btn-detail > span {
   width: max-content;
   font-size: initial;
   color: $color-main-text;
   padding-left: 25px;
   position: absolute;
   opacity: 0;
   visibility: hidden;
   transition: opacity .75s, padding-left .75s, visibility 0s linear .75s; }

.project__btn-detail:hover > span {
   padding-left: 60px;
   visibility: visible;
   opacity: 1;
   transition-delay: 0s; }

.project__btn-detail:before {
   position: absolute;
   content: '';
   height: 3px;
   width: 28px;
   background-color: #fff;
   top: 50%;
   left: 0; }

.project__btn-detail:hover.project__btn-detail:before {
   background-color: $color-sub-text;
   transition: all .75s ease; }

.project__btn-switch {
   position: absolute;
   top: 25px;
   right: 5%;
   font-size: 2em; }

.transition {
    transition: all .75s ease; }
.btn_disable {
    opacity: .35; }
.project_active .project__title {
   color: $color-sub-text;
   display: flex;
   align-items: center; }

.project_active {
   animation: fadeInLeft 1s ease-in; }

.project_active .project__preview {
   margin-left: 0; }

@keyframes fadeInLeft {
   0% {
      opacity: 0;
      transform: translate3d(-5%, 0, 0); }
   100% {
      opacity: 1;
      transform: none; } }
.project_hide {
    display: none; }

.project_show {
    display: grid; }

